@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaina+2:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	margin: 0;
	padding: 0;
}

body {
  /* background: #FBFBFD; */
  /* overflow-x: auto; */
  background-color: #343434;

}

.App {
	overflow-x: scroll;
	height: 100vh;
}

.board {
	display: flex;
	flex-direction: row;
	margin-right: 10em;
	width: 100%;
}


.empty-card {
	opacity: 0.8;
	min-width: 9em;
	width: 9em;
	outline: none;
	border: none;
}

.empty-card:focus-within {
	min-width: 20em;
	width: 20em;
}


.addCard-input {
	width: 100%;
	outline: none;
	border: none;
	padding: 1em;
	border-radius: 5px;
	background-color: #23374d;
	color: #fff;
}

.ReactModalPortal > div {
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity 200ms ease-in-out;

}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
    
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;

}

.modal {
    background: #333336;
    color: #fff;
    outline: none;
    width: 50em;
    height: 40em;
    border-radius: 10px;

}


.modal-title {
    padding: .5em;
    margin-top: 0;

}


.checklist-container {
    font-size: 1em;
    margin: 1em 2em;
    display: flex;
    flex-direction: column;
}

.checklist-title {
    font-size: 1.1em;

    display: flex;
    flex-direction: row;
}

.checklist-stats {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.list {
    display: flex;
    flex-direction: row;
}

.list-element {
    color: #fff;
    margin-left: 1em;
}

i {
    cursor: pointer;
}
.addlist {
    
}

.addlist-form {
    
}

.addlist-input {
    outline: none;
    border: none;
	padding: 1em;
	border-radius: 5px;
	background-color: #333336;
    color: #fff;
    cursor: pointer;
}

.addlist-input:hover {
    
    width: 100%;
    opacity: 1;
    text-decoration: underline;
}

.addlist-input:focus {
    color: #fff;
    text-decoration: none;
}
.checklist {
    background-color: #23374d;
    color: #fff;
    text-align: center;
    font-size: 1em;


}

.checklist h3 {
    border-bottom: 1px solid grey;
    padding: .5em;
    margin: 0 1em;

}

.checklist-form {
    display: flex;
    flex-direction: column;
    /* border: 1px solid red; */
    padding: 1em;
    
}

.checklist-input {
	border: none;
    padding: .5rem 0;
    margin: 1em;
    width: 7em;
	background-color: #385e88;
    color: #fff;
    border-radius: 5px;
    text-align: center;
}

.addChecklistBtn {
    outline: none;
    border: none;
    background-color: #385e88;
    color: #fff;
    width: 7em;
    border-radius: 5px;
    margin: auto;
    cursor: pointer;
    font-size: .9em;
}

.addChecklistBtn:hover {
    background-color: #4271a7;
    
}

.addChecklistBtn:active {
    background-color: #263f5c;
    
}
.modalHeader {
    border-bottom: 1px solid grey;
    margin: 1em;
}

.close-modal, .remove-modal, .icon {
    border: none;
    background: none;
    font-size: 1.7em;
    outline: none;
    margin: .2em;
    color: grey;

}

.icon:hover {
    color: lightgrey;
    cursor: pointer;
}

.icon:active {
    color: #5d6b81;
}


.task {
	border-radius: 5px;
	padding: .5em 0;
	/* background: #ecedf0; */
    background-color: #23374d;
	margin: .5em 0;
	font-size: 1em;
	color: white;
}

.task-title {
	cursor: pointer;
	margin: 1em;
	padding: .5em 0;
}

.task-input {
	width: 100%;
	outline: none;
	border: none;
	padding: 1em;
	margin-top: 1em;
	border-radius: 5px;
	background-color: #23374d;
	color: #fff;
	
}

.task-input:focus {
	border: 1px solid #343434;
	text-decoration: none;
	color: #fff;

}
.card {
	/* background-color: #FFFFFF; */
	/* border: 1px solid #cfd2da; */
    background-color: #636e72;
	border-radius: #0f4c75;
	color: white;
	border-radius: 3px;
    display: flex;
    flex-direction: column;
	margin: 1em;
	height: 100%;
	max-height: 80vh;
	overflow: hidden;
	overflow-y: auto;
}

.non-empty-card {
	min-width: 20em;
	min-height: 100%;
}

.card-content {
	margin: 1em;
}

.card-title {
	font-size: 1.3em;
}

.tasks {
	padding: 0;
	color: black;
}

